import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Home from "./pages/Home";
import Shop from "./pages/Shop";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import { ThemeProvider, Container } from "@material-ui/core";
import theme from "./theme";
import "./App.css";
import StadeDetail from './pages/StadeDetail';
import StadiumsPage from "./pages/owner/StadiumsPage";

function App() {
  const connection = true;

  return (
    <Router>
      <link
        rel="stylesheet"
        type="text/css"
        charset="UTF-8"
        href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css"
      />
      <link
        rel="stylesheet"
        type="text/css"
        href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css"
      />
      <ThemeProvider theme={theme}>
        <Navbar connected={connection} />
        <Container maxWidth="md">
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/shop" exact component={Shop} />
            <Route path="/stadiumDetail" exact component={StadeDetail} />
            <Route path="/stadiums" exact component={StadiumsPage} />
            {/* <Route path="/login" exact component={Login} /> */}

            {/* <Route component={NotFound} /> */}
          </Switch>
        </Container>
        <Footer />
      </ThemeProvider>
    </Router>
  );
}

export default App;
