import React, { Component } from "react";
import Slider from "react-slick";

export default class SimpleSlider extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
    };
    return (
      <div style={{ height: 300, marginTop: 32 }}>
        <Slider {...settings}>
          <div>
            <img
              style={{ width: "100%" }}
              src="data:image/jpeg;base64,"
            />
          </div>
          <div>
            <img
              style={{ width: "100%" }}
              src="data:image/jpeg;base64,"
            />
          </div>
          <div>
            <img
              style={{ width: "100%" }}
              src="data:image/jpeg;base64,"
            />
          </div>
        </Slider>
      </div>
    );
  }
}
